<template>
  <view class="container">
    <label class="title">检查日期</label>
    <view>
			<span
          @click="handleClickChangeDate"
          :class="[emrDate ? 'select-text__selected' : 'select-text__none']"
      >
        <image v-if="!emrDate" class="select-icon" src="@/static/icon-calendar.png"></image>
        {{ emrDate || '请选择日期' }}
      </span>
      <uni-calendar
          ref="calendar"
          :insert="false"
          @confirm="confirm"
      />
    </view>
  </view>
</template>

<script>
import uniCalendar from '@/packageSubPage/components/uni-calendar/uni-calendar.vue';

export default {
  name: 'dateInput',
  data() {
    return {
      emrDate: '',
    };
  },
  methods: {
    confirm(e) {
      this.emrDate = e.fulldate;
    },
    handleClickChangeDate() {
      this.$refs.calendar.open();
    }
  },
  components: {
    uniCalendar
  }
};
</script>

